/**
 * Suno Cover 
 *  Suno 
 */

// Ant Design Icons
import { CopyOutlined } from '@ant-design/icons';

// Ant Design Pro Components
import {
  PageContainer,
  ProCard,
  ProForm,
  ProFormList,
  ProFormSelect,
  ProFormText,
  ProFormTextArea,
} from '@ant-design/pro-components';

// Umi
import { history } from '@umijs/max';

// Ant Design Base Components
import {
  Alert,
  Button,
  Col,
  Flex,
  Form,
  Input,
  Modal,
  message,
  Row,
  Space,
  Spin,
} from 'antd';

// React
import React, { memo, useCallback, useEffect, useState } from 'react';
// 
import { ResultCard } from '@/components';

// Custom Hook
import { useApiKey } from '@/hooks/useApiKey';
import { container } from '@/services/ai/container';
import { DeepSeekService } from '@/services/ai/models/deepseekService';
import { GeminiService } from '@/services/ai/models/geminiService';
import { db } from '@/services/db';
import { mockGenerate } from '@/services/mockData';
// Custom Services and Types
import type {
  GenerateRequest,
  GenerateResponse,
  PromptRecord,
} from '@/shared/types';
// Shared Utils
import { copyToClipboard } from '@/shared/utils';

const SunoCover: React.FC = () => {
  // antdmessage hook
  const [messageApi, contextHolder] = message.useMessage();
  // hookAPI Key
  const {
    apiKey,
    model,
    isLoading: apiKeyLoading,
    saveApiKey,
    deleteApiKey,
    validateApiKey,
  } = useApiKey();

  // 
  const [form] = Form.useForm<GenerateRequest>();

  //  - 
  const [state, setState] = useState({
    loading: false, // 
    stylesResult: '', //  Styles 
    lyricsResult: '', //  Lyrics 
  });

  // 
  const updateState = useCallback((newState: Partial<typeof state>) => {
    setState((prev) => ({ ...prev, ...newState }));
  }, []);

  // API Key
  const checkApiKey = useCallback(() => {
    if (!apiKey) {
      Modal.confirm({
        title: ' AI API Key',
        content: '',
        okText: '',
        cancelText: '',
        onOk() {
          history.push('/ai-setting');
        },
      });
      return false;
    }
    return true;
  }, [apiKey, history]);

  // 
  const saveRecordToDB = useCallback(
    async (values: GenerateRequest, result: any) => {
      try {
        const userId = 1; // ID

        // 
        const referenceSongs = values.reference_songs
          .filter((song: { title?: string }) => song.title)
          .map(
            (song: { title: string; artist?: string }) =>
              `${song.title}${song.artist ? ` - ${song.artist}` : ''}`,
          );

        await db.createPromptRecord({
          userId,
          userInput: {
            songName: values.song_name,
            songLanguage: values.song_language,
            targetSinger: values.target_artist,
            referenceSongs,
            styleDescription: values.style_note || '',
            lyrics: values.lyrics_raw,
            scene: values.extra_note || '',
          },
          aiResult: {
            styles: result.styles,
            lyrics: result.lyrics,
            model: model, //  AI 
          },
          model: model, // 
        });
        console.log('');
        messageApi.success('');
      } catch (dbError) {
        console.error('', dbError);
      }
    },
    [model, messageApi],
  );

  /**
   * localStorageAPI Key
   * 
   */ // localStorage
  useEffect(() => {
    // 
    const selectedRecord = localStorage.getItem('selectedPromptRecord');
    if (selectedRecord) {
      try {
        const record: PromptRecord = JSON.parse(selectedRecord);

        // 
        form.setFieldsValue({
          song_name: record.userInput.songName, // 
          song_language: record.userInput.songLanguage,
          target_artist: record.userInput.targetSinger,
          reference_songs: record.userInput.referenceSongs.map(
            (song: string) => {
              const [title, artist] = song.split(' - ');
              return {
                title: title.trim(),
                artist: artist?.trim() || '',
              };
            },
          ),
          style_note: record.userInput.styleDescription,
          extra_note: record.userInput.scene,
          lyrics_raw: record.userInput.lyrics,
        });

        //  -  aiResult 
        updateState({
          stylesResult:
            record.aiResult?.styles || record.deepSeekResult?.styles || '', // 
          lyricsResult:
            record.aiResult?.lyrics || record.deepSeekResult?.lyrics || '', // 
        });

        // localStorage
        localStorage.removeItem('selectedPromptRecord');
      } catch (error) {
        console.error('', error);
      }
    }
  }, [form, updateState]);

  // AI
  const getAIService = () => {
    switch (model) {
      case 'deepseek':
        return container.resolve(DeepSeekService);
      case 'gemini':
        return container.resolve(GeminiService);
      default:
        throw new Error(`Unsupported AI model: ${model}`);
    }
  };

  /**
   * 
   *  AI  API 
   */
  const handleSubmit = useCallback(
    async (values: GenerateRequest) => {
      if (!checkApiKey()) return;

      updateState({ loading: true });
      try {
        //  DI 
        const aiService = getAIService();
        const result = await aiService.generate({ ...values, apiKey, model });

        updateState({
          stylesResult: result.styles,
          lyricsResult: result.lyrics,
        });

        messageApi.success('');

        // 
        const referenceSongs = values.reference_songs
          .filter((song: { title?: string }) => song.title)
          .map(
            (song: { title: string; artist?: string }) =>
              `${song.title}${song.artist ? ` - ${song.artist}` : ''}`,
          );

        // 
        const record = await db.createPromptRecord({
          userId: 1, // ID
          userInput: {
            songName: values.song_name,
            songLanguage: values.song_language,
            targetSinger: values.target_artist,
            referenceSongs,
            styleDescription: values.style_note || '',
            lyrics: values.lyrics_raw,
            scene: values.extra_note || '',
          },
          aiResult: {
            styles: result.styles,
            lyrics: result.lyrics,
            model: model,
          },
          model: model,
        });

        console.log('');
        messageApi.success('');
      } catch (error) {
        console.error(`${model} API `, error);
        const errorMessage =
          error instanceof Error
            ? error.message
            : ` ${model} API  API Key `;
        messageApi.error(errorMessage);
      } finally {
        updateState({ loading: false });
      }
    },
    [checkApiKey, updateState, apiKey, model, messageApi],
  );

  /**
   * 
   *  Styles  Lyrics 
   */
  const handleMockGenerate = useCallback(async () => {
    updateState({ loading: true });
    try {
      // 
      const result = await mockGenerate();

      // 
      updateState({
        stylesResult: result.styles,
        lyricsResult: result.lyrics,
      });

      messageApi.success('');

      // 
      const formValues = form.getFieldsValue();

      // 
      const referenceSongs = (formValues.reference_songs || [])
        .filter((song: any) => song.title)
        .map(
          (song: any) =>
            `${song.title}${song.artist ? ` - ${song.artist}` : ''}`,
        );

      // 
      const songLanguage = formValues.song_language || 'Mandarin';
      const targetSinger = formValues.target_artist || '';
      const lyrics = formValues.lyrics_raw || '';

      await db.createPromptRecord({
        userId: 1, // ID
        userInput: {
          songName: formValues.song_name,
          songLanguage,
          targetSinger,
          referenceSongs,
          styleDescription: formValues.style_note || '',
          lyrics,
          scene: formValues.extra_note || '',
        },
        aiResult: {
          styles: result.styles,
          lyrics: result.lyrics,
          model: 'mock', // 
        },
        model: 'mock', // 
      });

      console.log('');
      messageApi.success('');
    } catch (error) {
      console.error(':', error);
      messageApi.error('');
    } finally {
      updateState({ loading: false });
    }
  }, [form, updateState, messageApi]);

  // 
  const handleTitleClick = useCallback(() => {
    handleMockGenerate();
  }, [handleMockGenerate]);

  return (
    <>
      {contextHolder}
      <PageContainer>
        {/* AI API KeyAlert */}
        {!apiKey && (
          <Alert
            title=" AI API Key"
            banner
            style={{ marginBottom: 24 }}
            action={
              <Button type="link" onClick={() => history.push('/ai-setting')}>
                
              </Button>
            }
          />
        )}

        {/*  */}
        <Spin
          spinning={state.loading}
          fullscreen
          tip=""
          size="large"
        />

        <Row gutter={[24, 0]}>
          {/*  */}
          <Col span={8}>
            <ProCard
              title={
                <span onClick={handleTitleClick} style={{ cursor: 'pointer' }}>
                  
                </span>
              }
            >
              <ProForm
                form={form}
                layout="vertical"
                onFinish={handleSubmit}
                initialValues={{
                  song_language: 'Mandarin',
                }}
                submitter={{
                  render: () => (
                    <Flex vertical gap="small" style={{ marginTop: 16 }}>
                      {/*  */}
                      <Button
                        type="primary"
                        onClick={() => form.submit()}
                        loading={state.loading}
                        size="large"
                        block
                      >
                        
                      </Button>
                    </Flex>
                  ),
                }}
              >
                {/*  */}
                <ProFormText
                  name="song_name"
                  label=""
                  placeholder=""
                  rules={[{ required: true, message: '' }]}
                  fieldProps={{ showCount: true }}
                />

                {/*  */}
                <ProFormSelect
                  name="song_language"
                  label=""
                  placeholder=""
                  rules={[{ required: true, message: '' }]}
                  options={[
                    { value: 'Mandarin', label: '' },
                    { value: 'Cantonese', label: '' },
                    { value: 'Minnan', label: '' },
                    { value: 'English', label: '' },
                    { value: 'Korean', label: '' },
                    { value: 'Japanese', label: '' },
                    { value: 'Other', label: '' },
                  ]}
                />

                {/*  */}
                <ProFormText
                  name="target_artist"
                  label=""
                  placeholder=""
                  rules={[
                    { required: true, message: '' },
                  ]}
                  fieldProps={{ showCount: true }}
                />

                {/*  */}
                <ProFormList
                  name="reference_songs"
                  label=" 3 "
                  rules={[
                    {
                      validator: (_, songs) => {
                        if (songs && songs.length > 3) {
                          return Promise.reject(
                            new Error(' 3 '),
                          );
                        }
                        return Promise.resolve();
                      },
                    },
                  ]}
                  initialValue={[]}
                  creatorButtonProps={{
                    creatorButtonText: '',
                    type: 'dashed',
                    block: true,
                  }}
                  deleteIconProps={{
                    tooltipText: '',
                  }}
                  copyIconProps={false}
                  max={3}
                >
                  {(meta, _index, _action, _count) => (
                    <Space key={meta.key} style={{ width: '100%' }}>
                      <ProFormText
                        {...meta}
                        name={[meta.name, 'title']}
                        placeholder=""
                        rules={[
                          { required: true, message: '' },
                        ]}
                        style={{ marginBottom: 0, flex: 1 }}
                        fieldProps={{ style: { width: '100%' } }}
                      />
                      <ProFormText
                        {...meta}
                        name={[meta.name, 'artist']}
                        placeholder=""
                        style={{ marginBottom: 0, flex: 1 }}
                        fieldProps={{ style: { width: '100%' } }}
                      />
                    </Space>
                  )}
                </ProFormList>

                {/*  */}
                <ProFormTextArea
                  name="style_note"
                  label=""
                  placeholder=""
                  fieldProps={{ showCount: true, rows: 3 }}
                />

                {/*  */}
                <ProFormTextArea
                  name="extra_note"
                  label=""
                  placeholder=""
                  fieldProps={{ showCount: true, rows: 3 }}
                />

                {/*  */}
                <ProFormTextArea
                  name="lyrics_raw"
                  label=""
                  placeholder="[Verse][Chorus] "
                  rules={[
                    { required: true, message: '' },
                    { max: 2000, message: ' 2000 ' },
                  ]}
                  fieldProps={{
                    showCount: true,
                    autoSize: { minRows: 10, maxRows: 12 },
                  }}
                />
              </ProForm>
            </ProCard>
          </Col>

          {/* Styles */}
          <Col span={8}>
            <ResultCard
              title="Styles  Suno"
              value={state.stylesResult}
              onCopy={() => copyToClipboard(state.stylesResult, 'Styles')}
            />
          </Col>

          {/* Lyrics */}
          <Col span={8}>
            <ResultCard
              title="Lyrics  Suno"
              value={state.lyricsResult}
              onCopy={() => copyToClipboard(state.lyricsResult, 'Lyrics')}
            />
          </Col>
        </Row>
      </PageContainer>
    </>
  );
};

export default SunoCover;
