import { injectable } from 'tsyringe';
import { BaseAIService } from '../baseAIService';
import { GoogleGenAI } from '@google/genai';
import type { GenerateRequest, GenerateResponse } from '../../shared/types';
import { SYSTEM_PROMPT } from '../../../config/prompts';

@injectable()
export class GeminiService extends BaseAIService {
  /**
   * Gemini API
   */
  async generate(request: GenerateRequest): Promise<GenerateResponse> {
    const { apiKey } = request;

    // 
    if (!apiKey) {
      throw new Error('Gemini API key is required');
    }

    // 
    const userPrompt = this.generateUserPrompt(request);

    // 
    const systemPrompt = SYSTEM_PROMPT;

    try {
      // @google/genaiGemini API
      const genAI = new GoogleGenAI({ apiKey });

      const result = await genAI.models.generateContent({ 
        model: 'gemini-3-pro-preview',
        contents: [
          {
            parts: [
              {
                text: systemPrompt + '\n\n' + userPrompt
              }
            ]
          }
        ],
      });

      // Gemini API
      let aiResponse = '';
      
      if (result.candidates && result.candidates.length > 0) {
        const firstCandidate = result.candidates[0];
        if (firstCandidate.content && firstCandidate.content.parts) {
          // 
          aiResponse = firstCandidate.content.parts
            .filter(part => part.text)
            .map(part => part.text)
            .join('\n');
        }
      }

      // 
      return this.parseResponse(aiResponse);
    } catch (error) {
      console.error('Error calling Gemini API:', error);
      throw error;
    }
  }
}
